@import "~angular-material/angular-material.scss";

[ng\:cloak], [ng-cloak], [data-ng-cloak], [x-ng-cloak], .ng-cloak, .x-ng-cloak {
  display: none !important;
}

#login-container {
  height: 100%;

  > md-content {
    height: 100%;
    width: 100%;
    display: block;
    position: relative;
    overflow: hidden;
    background: #797979;

    &:before {
      content: ' ';
      display: block;
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      z-index: 1;
      opacity: 0.6;
      background-image: url('../../resources/assets/img/login-bg.jpg');
      background-repeat: no-repeat;
      background-position: 50% 0;
      background-size: cover;
      transition: all 400ms;
    }

    md-whiteframe {
      z-index: 100;
      position: relative;

      &.ng-enter {
        animation: bounceIn 1s;
      }
    }

    md-button {
      width: 100%;
    }
  }

  .errors {
    color: rgba(221, 44, 0, 0.9);
    background: rgba(221, 44, 0, 0.2);
    border: 1px solid rgba(221, 44, 0, 0.8);
    margin: 1em;
  }

  &.hovered {
    > md-content {
      &:before {
        filter: blur(1px);
      }
    }
  }
}



